
import React from 'react';
import { Link, Switch, Route, BrowserRouter, Sidebar, Redirect, useHistory } from "react-router-dom";

import Header from './components/Header';
import Footer from './components/Footer';

//route
import Main from './components/main/index';
import Products from './components/products/index';
import Datasets from './components/datasets/index';
import DatasetsDetail from './components/datasets/Detail';
import Company from './components/company/index';
import ContactUs from './components/contactUs/index';
import Talk from './components/talk/index';
import Terms from './components/terms/index';
import Privacy from './components/privacy/index';

function Routes({location}) {
  const history = useHistory();
  const searchParams = location.search ? location.search.split("=")[1] : "";
  if(searchParams) history.push(searchParams); 
  return (
    <BrowserRouter>
      <div className="App">
      <Header />
            <Switch>
              <Route exact path="/" component={Main} />
              <Route exact path="/Solutions" component={Main} />
              <Route path="/Products" component={Products} />
              <Route exact path="/Datasets" component={Datasets} />
              <Route path="/Datasets/detail" component={DatasetsDetail} />
              <Route path="/Company" component={Company} />
              <Route exact path="/ContactUs" component={ContactUs} />
              <Route exact path="/Talk" component={Talk} />
              <Route exact path="/Terms" component={Terms} />
              <Route exact path="/Privacy" component={Privacy} />
              <Redirect from="*" to="/" />
            </Switch>
        <section className="main_setcion3">
          <h2 className="title">Are you stylish than AI?</h2>
          <p className="radius"><Link to="/ContactUs" style={{color: 'white'}}>Request a demo</Link></p>
        </section>
        <Footer/>
      </div>
    </BrowserRouter>
  );
}

export default Routes;
